.projectHeader {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 20px;
}

.time {
  font-weight: 500;
  font-size: 14px;
  margin-bottom: 30px;
}

.attributes {
  margin-bottom: 20px;
}
.attributes span {
  font-weight: bold;
  font-size: 14px;
}

.photosContainer {
  width: 100%;
  min-height: 100px;
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
}

p, .text {
  font-weight: 500;
  font-size: 14px;
}

.text {
  margin-bottom: 20px;
}

.image {
  margin: 0;
  width: 200px;
  height: 200px;
  margin-right: 20px;
}

.technologyContainer {
  display: flex;
  flex-wrap: wrap;
}
.technologyContainer p {
  margin: 0;
  margin-right: 30px;
  margin-top: 5px;
}

@media screen and (max-width: 420px) {
  .photosContainer {
    flex-wrap: wrap;
  }

  .image {
    margin-bottom: 10px;
    width: 100%;
    height: 300px;
    margin-right: 30px;
  }
}