.navBar {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 100%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  z-index: 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
  color: white;
}

.Logo, .navItems {
  margin: 0;
}

.Logo {
  height: 66px;
  width: 66px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-image: url("/static/images/icons/Logo.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 1;
}
.Logo a {
  width: 100%;
  height: 100%;
}

.innerLogo {
  background: rgba(107, 92, 92, 0.54);
  border: 2px solid white;
  height: 80%;
  width: 160px;
}

.navLinks {
  display: flex;
  justify-content: space-between;
  margin: 0;
  height: 100%;
  flex-basis: 80%;
  align-items: center;
}

.navItems {
  width: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navItems a {
  margin: 0;
}

.navLinks a, .login a {
  color: inherit;
  text-decoration: inherit;
  font-size: 14px;
  font-weight: 500;
}

a, div.Logo {
  opacity: 0;
}

.showNav a, .showNav div {
  animation: slideDown 0.5s ease-in forwards;
  animation-delay: 0.2s;
}

@keyframes slideDown {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}