.fragmentHeader {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  margin-bottom: 40px;
}

.subHeader {
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin-bottom: 30px;
}

.time {
  font-weight: 500;
  font-size: 14px;
}

.text {
  font-weight: 500;
  font-size: 14px;
  margin-bottom: 25px;
}

.linkContainer {
  margin-bottom: 40px;
}

.ItemsContainer {
  width: 100%;
  margin-top: 45px;
  margin-bottom: 45px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
}

.link {
  opacity: 0.8;
  font-weight: 500;
  font-size: 14px;
  text-decoration: none;
}

.link:hover {
  opacity: 1;
}