  font-family: "Montserrat";
  src: url("/static/fonts/Montserrat-Italic.ttf");
  font-weight: 500;
  font-style: italic;
}
@font-face {
  font-family: "Montserrat";
  src: url("/static/fonts/Montserrat-Medium.ttf");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("/static/fonts/Montserrat-MediumItalic.ttf");
  font-weight: 600;
  font-style: italic;
}
@font-face {
  font-family: "Montserrat";
  src: url("/static/fonts/Montserrat-Bold.ttf");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("/static/fonts/Montserrat-BoldItalic.ttf");
