}
* {
  margin: 0 auto;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

html, body {
  font-family: "Montserrat";
}

a {
  color: #2E7BC1;
}

:global .hero {
  height: 100vh;
  width: 100%;
  padding: 100px 0;
  background-size: cover;
  z-index: -1;
}
:global section {
  width: 100%;
  margin-bottom: 90px;
}
:global .contentContainer {
  max-width: 800px;
  margin: 90px auto;
  height: auto;
}
:global .row {
  margin: 0;
  margin-bottom: 10px;
  width: 100%;
  min-height: 200px;
  display: flex;
  justify-content: space-between;
  align-content: center;
}
:global .rowPhotoPortrait, :global .rowPhotoLandscape {
  margin: 0;
  background-size: cover;
  background-position: center;
}
:global .rowPhotoPortrait {
  width: 190px;
  height: 240px;
}
:global .rowPhotoLandscape {
  width: 395px;
  height: 240px;
}
@media screen and (max-width: 770px) {
  :global .contentContainer {
    max-width: 700px;
    margin: 70px auto;
    height: auto;
  }
  :global .rowPhotoPortrait {
    width: 170px;
    height: 220px;
    background-repeat: no-repeat;
  }
  :global .rowPhotoLandscape {
    width: 345px;
    height: 220px;
    background-repeat: no-repeat;
  }
}
@media screen and (max-width: 420px) {
  :global .contentContainer {
    width: 360px;
    margin-top: 80px;
    margin-bottom: 100px;
    height: auto;
  }
  :global section {
    width: 100%;
    margin: 0px;
  }
  :global .row {
    flex-wrap: wrap;
  }
  :global .rowPhotoPortrait {
    width: 100%;
    height: 450px;
    margin-bottom: 10px;
  }
  :global .rowPhotoLandscape {
    width: 100%;
    height: 250px;
    margin-bottom: 10px;
  }
}
@media screen and (max-width: 360px) {
  :global .contentContainer {
    width: 310px;
    margin-top: 80px;
    margin-bottom: 100px;
    height: auto;
  }
  :global .rowPhotoPortrait {
    width: 100%;
    height: 400px;
  }
  :global .rowPhotoLandscape {
    width: 100%;
    height: 200px;
  }
}