.tabContainer {
  border-top: black solid 1px;
  border-bottom: black solid 1px;
  width: 100%;
  height: 50px;
  margin-top: 90px;
  display: flex;
  align-items: center;
}
.tabContainer a {
  color: #000000;
  text-decoration: none;
  font-weight: 500;
  font-size: 18px;
  opacity: 1;
}
.tabContainer a:hover {
  opacity: 0.5;
  transition: opacity 0.2s;
  cursor: pointer;
}
.tabContainer .active {
  text-decoration: underline;
}

@media screen and (max-width: 500px) {
  .tabContainer {
    margin-top: 50px;
  }
}
@media screen and (max-width: 400px) {
  .tabContainer {
    margin-top: 50px;
  }
}